// components/Hero.js
import Button from './Button';
import Image from 'next/image';

export default function Hero({ 
  title = "Premium medizinisches Fachpersonal für Ihre individuellen Bedürfnisse",
  subtitle = "Professionelle Vermittlung von qualifizierten Ärzten, Pflegekräften und medizinischen Spezialisten",
  buttonText = "KONTAKT AUFNEHMEN",
  buttonLink = "/kontakt",
  showImage = true
}) {
  return (
    <section className="bg-black-custom py-5">
      <div className="container py-5">
        <div className="row align-items-center">
          <div className={`${showImage ? 'col-lg-6' : 'col-lg-8 mx-auto text-center'} mb-5 mb-lg-0`}>
            <h1 className="display-4 fw-bold mb-4">{title}</h1>
            <p className="lead mb-5">{subtitle}</p>
            <Button href={buttonLink} className="px-4 py-2">{buttonText}</Button>
          </div>
          
          {showImage && (
            <div className="col-lg-6">
              <div className="bg-dark-gray p-5 text-center rounded position-relative" style={{minHeight: '300px'}}>
                {/* Hier könnte ein echtes Bild eingefügt werden */}
                <p className="text-muted">Bild: Professionelles medizinisches Personal</p>
              </div>
            </div>
          )}
        </div>
      </div>
    </section>
  );
}